/* Styles for the category nav */
.page-id-19 {
    .category-nav {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 2rem;

        .accordion-icon {
            width: 2rem;
            height: 2rem;
            fill: black;
            transition: transform 0.3s ease;
            transform-origin: center;
            position: absolute;
            top: 50%;
            right: 4px;
            transform: translateY(-50%) rotate(0deg);

            &.active {
                transform: translateY(-50%) rotate(180deg);
            }
        }


        .subnav-title {
            cursor: pointer;
            margin: 0;
            width: 15rem;
            border: solid 3px #000;
            padding: 1rem;
            display: flex;
            position: relative;
            background: $color__background-body;

            @media (min-width: 68rem){
                display: none;
                visibility: none;
                }

            h3{
                margin: 0;
            }

            &:focus{
                background: #eeeeee;
            }
        }

        .dropdown{
            display: none;
            list-style: none;
            padding: 0;
            margin: 0;
            width: 15rem;
            position: absolute;
            top: 3.4rem;
            background: $color__background-body;

            @media (min-width: 68rem){
                display: flex;
                border: 0;
                position: relative;
                justify-content: center;
                top: 0;
                width: 100%;
                max-width: 87.5rem;
                margin: 0 0 1.5rem;
            }

            li{
                @media (min-width: 68rem){
                padding: 0 1rem;
                }
            }

            a{
                display: block;
                width: 15rem;
                text-align: center;
                border: solid 3px #000;
                border-top: none;
                padding: 0.8rem 0;

                &:focus{
                    background: #eeeeee;
                }

            @media (min-width: 68rem){
                border: none;
                width: auto;
                padding: 0;
                text-decoration: none;

                &:focus, &:hover{
                    background: auto;
                    text-decoration: underline;
                }
            }

            }
        }

        .active{
        display: block;
        display: flex;
        flex-direction: column;
        align-items: center;
        }
    
    }
}
  