/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #313131;
	font-family: "Manrope", "sans-serif";
	font-size: 1rem;
	line-height: 1.5;
	letter-spacing: 0.6px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	color: #1a1a1a;
	font-family: "Playfair Display", "Times New Roman";
	letter-spacing: normal;
}

h1 {
	font-size: 3rem;
	text-align: center;
	margin: 3rem 0;
}

@media (min-width: 50em) {
	h1 {
		font-size: 4rem;
	}
}
p {
	margin-bottom: 1.5em;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
html {
	overflow-x: hidden;
}

body {
	background: #fff;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/* Links
--------------------------------------------- */
a {
	color: #1a1a1a;
}
a:visited {
	color: #313131;
}
a:hover, a:focus, a:active {
	color: #313131;
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

.link-spacing {
	letter-spacing: 2.4px;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid #313131;
	border-radius: 3px;
	border-color: #313131;
	background-color: #313131;
	color: #fff;
	line-height: 1;
	padding: 1em 1.5em;
	cursor: pointer;
	transition: 0.5s;
}
button:hover, button:focus,
input[type=button]:hover,
input[type=button]:focus,
input[type=reset]:hover,
input[type=reset]:focus,
input[type=submit]:hover,
input[type=submit]:focus {
	box-shadow: inset 0 0 0 2em #fff;
	border: 1px solid #313131;
	color: #313131;
}
button:active,
input[type=button]:active,
input[type=reset]:active,
input[type=submit]:active {
	border: 1px dotted;
}

.btn-border {
	border: 2px solid #313131;
	padding: 0.5rem 1.5rem;
	text-decoration: none;
	text-transform: uppercase;
	font-weight: 700;
	font-size: 0.8rem;
}
.btn-border:hover {
	border-color: #7a7a7a;
	color: #7a7a7a;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #313131;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.site {
	max-width: 1920px;
	margin: 0 auto;
}

.site-main {
	overflow: hidden;
	padding: 0 1.5rem;
}

@media screen and (min-width: 50em) {
	.site {
		display: grid;
		grid-template-columns: 1fr minmax(0, 1000px) 1fr;
		grid-template-areas: "header header header" ". main ." "footer footer footer";
	}
	.site-header {
		grid-area: header;
	}
	.site-main {
		grid-area: main;
	}
	.site-footer {
		grid-area: footer;
	}
}
/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Header
--------------------------------------------- */
.page-id-32 .site-header {
	max-width: 1920px;
	margin: 0 auto;
}

.site-header {
	background-color: transparent;
	position: relative;
	border-bottom: 1.5px solid #C1C1C1;
	z-index: 10000;
	height: 63px;
}

body.home.admin-bar .site-header {
	top: 46px;
}

@media screen and (min-width: 48.9em) {
	body.home.admin-bar .site-header {
		top: 32px;
	}
}
.site-branding {
	width: 3rem;
	max-height: 100%;
	padding: 0.5rem 0;
	margin: 0 1rem;
}
.site-branding p {
	margin: 0;
}
.site-branding a {
	position: relative;
	z-index: 7;
}

.home .site-header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 50;
	color: #fff;
	border-color: #fff;
}
.home .site-header.toggled {
	background-color: #eeeeee;
	border-color: #1a1a1a;
}
.home .site-header.toggled .menu-toggle svg {
	fill: #1a1a1a;
}

@media screen and (min-width: 59.375em) {
	.site-header {
		display: grid;
		grid-template-columns: auto 1fr;
		gap: 1rem;
		padding: 0rem 2rem;
	}
	.site-branding {
		grid-column: 1/2;
	}
}
/* Navigation
--------------------------------------------- */
.main-navigation {
	display: block;
	width: 100%;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
	text-align: center;
	background-color: #eeeeee;
	text-transform: uppercase;
	font-size: 1.2rem;
}
.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}
.main-navigation ul ul li:hover > ul, .main-navigation ul ul li.focus > ul {
	display: block;
	left: auto;
}
.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}
.main-navigation li {
	position: relative;
}
.main-navigation li:hover > a, .main-navigation li.focus > a {
	background-color: #fff;
}
.main-navigation li:hover > a svg, .main-navigation li.focus > a svg {
	fill: #313131;
}
.main-navigation a {
	display: block;
	text-decoration: none;
	padding: 1rem;
}
.main-navigation .current_page_item > a,
.main-navigation .current-menu-item > a,
.main-navigation .current_page_ancestor > a,
.main-navigation .current-menu-ancestor > a {
	background-color: #fff;
}

.menu-toggle {
	position: absolute;
	padding: 0.8em 1em 0.4em;
	top: 0;
	z-index: 1100;
	right: 0;
	background: transparent;
	border: none;
	cursor: pointer;
}
.menu-toggle:hover, .menu-toggle:focus {
	box-shadow: none;
	border: none;
}
.menu-toggle svg {
	fill: #1a1a1a;
	width: 40px;
	height: auto;
}

.home .menu-toggle svg {
	fill: #fff;
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

.menu-header-menu-container {
	position: relative;
	z-index: 1000;
}

.shop-navigation {
	display: inline-block;
}

.menu-shop-container {
	z-index: 1000;
}
.menu-shop-container svg {
	display: none;
}

@media screen and (min-width: 59.375em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation {
		display: flex;
		justify-content: space-between;
	}
	.main-navigation ul {
		background-color: transparent;
		font-size: 1rem;
		display: flex;
	}
	.main-navigation li:hover > a, .main-navigation li.focus > a {
		background-color: #eeeeee;
	}
	.main-navigation .current_page_item > a,
	.main-navigation .current-menu-item > a,
	.main-navigation .current_page_ancestor > a,
	.main-navigation .current-menu-ancestor > a {
		font-weight: 700;
		background-color: transparent;
	}
	.main-navigation a {
		padding: 1.4rem 1rem 1rem;
	}
	.home .main-navigation ul {
		background-color: transparent;
	}
	.home .main-navigation a {
		color: #fff;
	}
	.home .main-navigation li:hover > a, .home .main-navigation li.focus > a {
		color: #1a1a1a;
	}
	.home .main-navigation li:hover > a svg, .home .main-navigation li.focus > a svg {
		fill: #1a1a1a;
	}
	.home .main-navigation svg {
		fill: #fff;
	}
	.menu-header-menu-container {
		display: inline-block;
		width: fit-content;
	}
	.menu-shop-container {
		display: inline-block;
	}
	.menu-shop-container svg {
		display: inline;
		width: 22px;
		vertical-align: middle;
		fill: #1a1a1a;
	}
	.menu-shop-container svg:first-child {
		width: 25px;
		height: auto;
	}
	.menu-shop-container .shop-menu-text {
		display: none;
	}
	.menu-shop-container a {
		padding: 1.3rem 1rem 1rem;
	}
}
.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}

/* Styles for the category nav on services/shop page */
.category-nav {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.category-nav .accordion-icon {
	width: 2rem;
	height: 2rem;
	fill: black;
	transition: transform 0.3s ease;
	transform-origin: center;
	position: absolute;
	top: 50%;
	right: 4px;
	transform: translateY(-50%) rotate(0deg);
}
.category-nav .accordion-icon.active {
	transform: translateY(-50%) rotate(180deg);
}
.category-nav .subnav-title {
	cursor: pointer;
	margin: 0;
	width: 15rem;
	border: solid 2px #1a1a1a;
	padding: 1rem;
	display: flex;
	position: relative;
	background: #fff;
	font-weight: 600;
	color: #000;
}
@media (min-width: 68rem) {
	.category-nav .subnav-title {
		display: none;
		visibility: none;
	}
}
.category-nav .subnav-title:focus {
	background: #eeeeee;
}
.category-nav .dropdown {
	display: none;
	list-style: none;
	padding: 0;
	margin: 0;
	width: 15rem;
	position: absolute;
	top: 51px;
	background: #fff;
}
@media (min-width: 68rem) {
	.category-nav .dropdown {
		display: flex;
		border: 0;
		position: relative;
		justify-content: center;
		top: 0;
		width: 100%;
		max-width: 87.5rem;
		margin: 0 0 1.5rem;
	}
}
@media (min-width: 68rem) {
	.category-nav .dropdown li {
		padding: 0 1rem;
	}
}
.category-nav .dropdown a {
	display: block;
	width: 15rem;
	text-align: center;
	border: solid 2px #1a1a1a;
	border-top: none;
	padding: 0.8rem 0;
	text-decoration: none;
}
.category-nav .dropdown a:focus, .category-nav .dropdown a:hover {
	text-decoration: underline;
}
@media (min-width: 68rem) {
	.category-nav .dropdown a {
		border: none;
		width: auto;
		padding: 0;
		text-decoration: none;
	}
	.category-nav .dropdown a:focus, .category-nav .dropdown a:hover {
		background: auto;
		text-decoration: underline;
	}
}
.category-nav .active {
	display: block;
	display: flex;
	flex-direction: column;
	align-items: center;
}

